button.btn {

}

:root {
    --btn-bg: #ef5350;
    --btn-hover: #e86765;
    --btn-border: #c62828;
    --btn-bg-disabled: #cb4240;
    --btn-active: #ff9999;
}

.btn.blue {
    --btn-bg: #2196f3;
    --btn-hover: #42a5f5;
    --btn-border: #1565c0;
    --btn-bg-disabled: #1875c7;
    --btn-active: #90caf9;
}

button.btn, .btnlike {
    background: var(--btn-bg);
    border-radius: 4px;
    border: none;
    border-bottom: 4px solid var(--btn-border);
    padding: 8px 12px;
    min-width: 40px;
    cursor: pointer;
    color: white;
    margin: 3px;
    margin-left: 0px;

}


button[large] {
    padding: 10px 14px;
    min-width: 200px;
}

button.btn:not([disabled]):active, .btnlike:active, .btnlike:focus {
    border-top: 4px solid var(--btn-active);
    border-bottom-width: 0px;
    vertical-align: -4px;
}

.btnGroup .btn:hover, .btn:hover {
    background: var(--btn-hover) !important;
    border-color: var(--btn-active) !important;
}

.btnGroup.btnVertical .btn {
    border-bottom: 0;
    border-right: 4px solid var(--btn-border);
}


button.btn[disabled], .btn[disabled]:hover {
    cursor: not-allowed;
    background: var(--btn-bg-disabled);
    border-bottom: 4px solid var(--btn-border) !important;

}

.btnGroup.btnVertical .btn:active, .btnGroup.btnVertical .btn:active, .btnGroup.btnVertical .btn:focus {
    border: none;
    border-left: 4px solid var(--btn-active);
}

.btnGroup {
    display: inline-block;
    margin: 0 10px;
}

.btnGroup .btn {
    margin: 0 0px;
    border-radius: 0px;
    border-left: 1px solid var(--btn-border);
}

.btnGroup .btn:first-child {
    border-radius: 4px 0 0 4px;
    border-left: none;
}

.btnGroup .btn:last-child {
    border-radius: 0px 4px 4px 0px;
}


.btnGroup.btnVertical .btn {
    border-radius: 0;
}

.btnGroup.btnVertical {
    margin: 0;
}

button.btn.btnSmall {
    padding: 5px 10px;
    border-bottom-width: 2px;
    min-width: 30px;
}

button.btn.btnSmall:not([disabled]):active, .btnlike:active, .btnlike:focus {
    border-top-width: 2px;
    vertical-align: -2px;
}

